* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, 'Apple Color Emoji',
        Arial, sans-serif, 'Segoe UI Emoji', 'Segoe UI Symbol';
    font-size: 16px;
    margin: 0;
    padding: 0;
}

.mw-editsection {
    display: none;
}

:root {
    --background: #ececec;
    --text-color: black;
    --content-background: white;
    --border-color: #ddd;
    --content-width: 700px;
}

@media @dark {
    :root {
        --background: #171717;
        --text-color: #dedede;
        --content-background: #232323;
        --border-color: #444;
    }
}

.infobox {
    background: #f4f4f4;
    float: right;
    padding: 15px;
    @media @dark {
        background: #333333;
    }
}

body {
    background: var(--background);
    color: var(--text-color);
}

html,
body {
    height: 100%;
}

#root,
.container {
    height: 100%;
}

@dark: ~'(prefers-color-scheme: dark)';

a {
    color: #0366d6;
    @media @dark {
        color: #91c5ff;
    }

    text-decoration: none;
    &:hover {
        text-decoration: underline;
    }
}
.container {
    display: flex;
    // overflow-x: scroll;
}

.open-sidebar {
    position: absolute;
    bottom: 10px;
    left: 10px;
    padding: 10px;
    cursor: pointer;
    color: gray;
    z-index: 1000;
}
.sidebar {
    width: 200px;
    overflow: auto;
    flex-shrink: 0;
    display: flex;
    height: 100%;
    flex-direction: column;
    transition: margin-left 0.2s linear;

    &.hidden {
        margin-left: -200px;
    }

    .logo {
        font-size: 140%;
        padding: 20px;
        font-weight: 800;
        border-bottom: 1px solid var(--border-color);
    }

    .menu {
        padding: 10px 0;
        border-bottom: 1px solid var(--border-color);
    }

    .favorites {
        padding: 10px 0;
        flex: 1;
        overflow: auto;
    }
    .item {
        padding: 10px 20px;

        cursor: pointer;

        &.selected {
            background: #776a69;
            font-weight: 600;
            color: white;
        }
    }
    .footer {
        padding: 20px;
        color: gray;
        cursor: pointer;
    }
}

@media (max-width: 940px) {
    .sidebar,
    .open-sidebar {
        display: none;
    }
}

.pane {
    background: var(--content-background);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

    @media @dark {
        // box-shadow: none;
        border-left: 1px solid var(--border-color);
    }

    flex-shrink: 0;
    flex-grow: 1;
    overflow-y: scroll;
    overflow-x: hidden;
    max-width: 100%;
    padding: 0 20px;

    .header {
        margin: 0 auto;
        position: sticky;
        top: 0;
        background: var(--content-background);

        font-size: 150%;
        border-bottom: 1px solid var(--border-color);
        padding: 20px 0px;
        z-index: 999;

        .close {
            border: 0;
            background: transparent;
            font: inherit;
            display: inline-block;
            float: right;
            cursor: pointer;
            color: inherit;
        }
    }

    .content,
    .header {
        max-width: 100%;
        width: 700px;
        margin: 0 auto;

        @media (max-width: 1450px) {
            width: 550px;
        }
    }
}
